export const listCataloges = async (teacherid) => {
  try {
    let querySnapshot = await app.firestore().collection('repo/library/users').doc(teacherid).collection('catalogs').orderBy('created_at','desc').get();
    if (querySnapshot.docs) {
      const catalogs = querySnapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data()
        };
      });
      return catalogs;
    }
  } catch (err) {
    console.log(err);
  }
};

