export const updateCatalog = async (data) => {
  console.log(data.catalogId);
  console.log(data);
  try {
    const detail = data.detail;
    delete data.detail;
    await app.firestore().collection('repo/library/users').doc(data.created_by).collection('catalogs').doc(data.catalogId).update(data);
    await app.firestore().collection('repo/library/users').doc(data.created_by).collection('catalogs').doc(data.catalogId).collection('detail').doc('content').set({ data: detail });
    doIndex(data.catalogId,data.created_by);
    console.log(data);
    return true;
  } catch (err) {
    console.log(err);
  }
};

export const createCatalog = async (data) => {
  try {
    const detail = data.detail;
    delete data.detail;
    console.log(data);
    await app.firestore().collection('repo/library/users').doc(data.created_by).set({author:data.author})
    let catalogRef  = await app.firestore().collection('repo/library/users').doc(data.created_by).collection('catalogs').add(data);
    await catalogRef.collection('detail').doc('content').set({ data: detail });
    
    doIndex(catalogRef.id,data.created_by);
    console.log(data);
    return true;
  } catch (err) {
    console.log(err);
  }
};

