export const addCatalogSection = async (name,catalogId,teacherid)=>{
  const lastSection = await db.collection('repo/library/users').doc(teacherid).collection('catalogs').doc(catalogId).collection("sections").orderBy("index","desc").limit(1).get();
  if(lastSection.empty){
    await db.collection('repo/library/users').doc(teacherid).collection('catalogs').doc(catalogId).collection("sections").add({index:1,name:name});
  } else {
    lastSection.forEach(async section=>{
      let data = section.data();
      let index = data.index+1;
      await db.collection('repo/library/users').doc(teacherid).collection('catalogs').doc(catalogId).collection("sections").add({index:index,name:name});

    window.location.reload();
    })
  }
  return "done";
};

