export const deleteQuiz = async(userId, catalogId, quizId, sectionId,activityType)=>{
    console.log(activityType);
  try{
    let quizref ='';
    if(activityType==='quiz'){
       quizref = "teachers/"+userId+"/quizzes/"+quizId;
    }else if( activityType==='theory'){
       quizref = "teachers/"+userId+"/theories/"+quizId;
    }

    console.log(quizref);
    const sectionRef =  await db.collection('repo/library/users').doc(userId).collection('catalogs').doc(catalogId).collection("sections").doc(sectionId);
    const doc = await sectionRef.get();
    if (!doc.exists) {
      console.log('No such document!');
    } else {
      console.log('Document data:', doc.data());
      const sectionData = doc.data().activities;
      if(sectionData.length > 0){
        for (const [index,element] of sectionData.entries()) {
          console.log(element.ref.path);
          if(element.ref.path.toString() === quizref){

            console.log(index);
            sectionData.splice(index,1);
            doc.ref.update({
              activities:sectionData
            });
            if(activityType==='quiz'){
              await db.collection('repo/library/users').doc(userId).collection("quizzes").doc(quizId).delete();
            }else if(activityType==='theory'){
              await db.collection('repo/library/users').doc(userId).collection("theories").doc(quizId).delete();
            }
          }
        }
      }
    }

  }catch(error){
    console.log(error);
  }
};

export const addUrlActivity = async(values,catalogId,sectionId,teacherid)=>{
  console.log(values)
  console.log(catalogId)
  console.log(teacherid)
  const activity={name:values.name,url:values.url,type:values.type}
  await db.collection('repo/library/users').doc(teacherid).collection('catalogs').doc(catalogId).collection("sections").doc(sectionId).update({activities:firebase.firestore.FieldValue.arrayUnion(activity)});
  return "done";
};

