export const doPublic = async ( catalogId,teacherId)=>{
  var param = {
    catalogId: catalogId,
    teacherId:teacherId
  };
  //renew idToken
  await axios.post(`${API_URL}/api/elastic`,param);
  return "success";
}

export const doIndex = async ( catalogId,teacherId)=>{
  var param = {
    catalogId: catalogId,
    teacherId:teacherId
  };
  //renew idToken
  await axios.post(`${API_URL}/api/elastic_index`,param);
  return "success";
}
