import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import { listCataloges } from '../../../../api/catalog';
import useAuth from '../../../../hooks/useAuth';


const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const CatalogListView = () => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [catalog, setCatalogzes] = useState([]);
  const { user } = useAuth();
  const getCatalogzes = useCallback(async () => {
    try {
      const catalog = await listCataloges(user.userId);
      
      if (isMountedRef.current) {
        setCatalogzes(catalog);
        console.log(catalog);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getCatalogzes();}, [getCatalogzes]);
  return (
    <Page className={classes.root} title="Trường học">
      <Container maxWidth={false}>
        <Header/>
        <Box mt={3}>
          <Results catalogs={catalog}/>
        </Box>
      </Container>
    </Page>
  );
};

export default CatalogListView;
